import * as React from 'react';
import FlatList from "flatlist-react";
import randomstring from 'randomstring';
import '../assets/fonts/fontawesome/css/all.min.css';
import base64 from 'base-64';
import { endpoint } from "../services/endpoint";
import { Call, _onfetchProductsByCategory } from '../services/fetchingdata';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import Pagination from 'react-responsive-pagination';
import { WhenFailed } from '../components/whenfailed/Whenfailde';
import { WhenEmpty } from '../components/whenempty/Whenempty';
import { Item } from '../components/renderitem/RenderItem';

export const MarketScreen = (navigation) => {

    const [products, setproducts] = React.useState([]);
    const [currency, setcurrency] = React.useState("USD $");
    const [title, settitle] = React.useState("");
    const [currentPage, setCurrentPage] = React.useState(1);
    const [categ, setcateg] = React.useState(0);
    const [isfailed, setisfailed] = React.useState(false);
    const [isloading, setisloading] = React.useState(true);

    const ftc = async ({ categ, offset }) => {
        setisloading(true)
        Call.external({
            url: `/produits/list/by/${categ}`,
            method: "GET",
            params: {
                limit: 10,
                offset
            }
        }, (err, done) => {
            setisloading(false)
            if (done) {
                const { status, data, message } = done;
                if (status === 200) {
                    const { list, length, idcategory, __tbl_category } = data;
                    const { category } = __tbl_category;
                    settitle(category)
                    setisfailed(false)
                    setproducts(list)
                } else {
                    setisfailed(true)
                    setproducts([])
                }
            } else {
                setisfailed(true)
            }
        })
    };

    React.useEffect(() => {
        const categ = navigation['match']['params']['categ']
        ftc({ categ, offset: 0 })
    }, []);

    return (
        <>
            <section className="section-name padding-y-sm">
                <div className="container">
                    <header className="section-heading">
                        <a href="/#" className="btn btn-outline-primary float-right">
                            <span className="fa fa-list pr-2"></span>
                            <span>Voir plus</span>
                        </a>
                        <h3 className="section-title">{`Produits > `}<b className="text-muted text-capitalize">{`${title}`}</b> </h3>
                        <p>Nous selectionnons des produits les plus pertinents pour vous </p>
                    </header>
                    <div className="row">
                        {isfailed
                            ? <WhenFailed />
                            :
                            <>
                                <FlatList
                                    list={products}
                                    renderWhenEmpty={() => <WhenEmpty />}
                                    renderItem={(item) => <Item item={item} />}
                                    keyExtractor={(item) => item['id'].toString()}
                                />
                                <div className="col-lg-12 pt-3">
                                    <Pagination
                                        current={currentPage}
                                        total={products && Math.floor(products.length / 32)}
                                        onPageChange={(page) => { setCurrentPage(page); ftc(categ, page) }}
                                    />
                                </div>
                            </>
                        }
                    </div>
                </div>
            </section>
        </>
    )
}