import * as React from 'react';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import FlatList from "flatlist-react";
import { endpoint } from '../services/endpoint';
import { Dropdown } from 'react-bootstrap';


export const CartScreen = () => {

    const [items, setitems] = React.useState([]);
    const [total , settotal] = React.useState(0);
    const [iscdf, setcdf] = React.useState(false); // 1 means CDF : 2 means USD
    // const [isloading, setisloading] = React.useState(false);
    const performCommande = (e) => {
        e.preventDefault();

        toast.info("Fonctionalité temporairement down!", {
            position: "bottom-right",
            autoClose: 5000,
            hideProgressBar: true,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined
        })
    }
    const whenEmpty = () => {
        return(
            <>
                <div className="w-100 d-flex justify-content-center my-5 py-5">
                    <div className="col-lg-12 text-center">
                        <h4 className="fa fa-dropbox display-5 text-muted" style={{fontSize: "92px"}}> </h4>
                        <h5>Aucune information pour l'instant</h5>
                        <h6>Les articles que vous avez commander serons visibles ici</h6>
                    </div>
                </div>
            </>
        )
    };
    const RenderLi = (li) => {
    const [renderinsvisble, setisvisible] = React.useState(false);
    const m = li['item'];
    // settotal(total + parseFloat(m['price']))
    return (
        <li class={`list-group-item align-items-center ${renderinsvisble ? "d-none" : "" }`} key={`${m['id']}`}>
            <div className="row">
                <div className="col-lg-2">
                    <img src={`${endpoint}/services/ressources/${m['imgcover']}`} alt="" srcSet="" className="w-100" />
                </div>
                <div className="col-lg-10">
                    <div className="">
                        <b className="text-uppercase pb-0">{m['name']}</b>
                        <h6 className="text-muted text-uppercase">{m['__tbl_category']['categorie']}</h6>
                        <h6><span className="text-muted">PRIX : </span><span className="text-danger">$ {m['price']}</span></h6>
                    </div>
                    <div className="py-2">
                        Qté : 
                        <span class="badge badge-primary badge-pill ml-2">{li['qte']}</span>
                    </div>
                    <div>
                        <div className="btn-group btn-group-sm">
                            <button className="btn btn-primary">
                                <span className="fa fa-code-fork pr-2"></span>
                                <span>Commander</span>
                            </button>
                            <button className="btn btn-danger" onClick={e => {
                                e.preventDefault();
                                setisvisible(true)
                            }}>
                                <span className="fa fa-trash pr-2"></span>
                                <span>Supprimer de la liste</span>
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        </li>
    )}
    const ftc = () => {
        const articles = localStorage.getItem("memo-user-articles");
        if(articles){
            let tt = 0;
            const arts = JSON.parse(articles)

            setitems(arts)
            arts.map((val) => {
                console.log(val['item']['price']);
                tt += val['item']['price'];
            });
            settotal(tt)
            // toggleCurrecncy(iscdf ? 1 : 2);
        }else console.log("the script spipped");
    }
    const toggleCurrecncy = (curre) => {
        if(iscdf){
            if(curre === 1) settotal(total);
            if(curre === 2) {
                settotal(parseFloat(total / 2000));
                setcdf(false);
            }
        }else{
            if(curre === 1) {
                settotal(total * 2000);
                setcdf(true);
            }
            if(curre === 2) settotal(total);
        }
    }
    React.useEffect(() => {
        if(!localStorage.getItem("memo-user")) window.location.replace("/client/login");
        ftc()
    }, [])

    return (
        <>
            <div className="container py-5 my-2">
                <div className="card">
                    <div className="card-header d-flex justify-content-between">
                        <h6><span className="fa fa-shopping-cart pr-3"></span>Panier</h6>
                        <div className="col-8 d-flex justify-content-end px-0">
                            {/* <h6 className="text-muted"><span className="pr-3"></span>Articles dans le panier <span className="badge badge-light">{items && items.length}</span></h6> */}
                            <Dropdown className="px-0">
                                <Dropdown.Toggle variant="light" id="dropdown-basic" className="px-3">
                                        <span className="badge badge-light mr-2">Devise</span>
                                        <span>{iscdf ? "CDF" : "USD"}</span>
                                </Dropdown.Toggle>

                                <Dropdown.Menu>
                                    <Dropdown.Item href="#/action-1" onClick={() => toggleCurrecncy(1)}>
                                        <span className="badge badge-light mr-2">CDF</span>
                                        <span>Francs Congolais</span>
                                    </Dropdown.Item>
                                    <Dropdown.Item href="#/action-2" onClick={() => toggleCurrecncy(2)}>
                                        <span className="badge badge-light mr-2">USD</span>
                                        <span>Dollars Americains</span>
                                    </Dropdown.Item>
                                </Dropdown.Menu>
                            </Dropdown>
                        </div>
                    </div>
                    <div className="card-body">
                        <ul class="list-group">
                            {/* ----------------------------- */}
                            <FlatList 
                                list={items}
                                renderWhenEmpty={whenEmpty}
                                renderItem={RenderLi}
                                keyExtractor={(item) => item['id'].toString()}
                            />
                            {/* ----------- cart item --------- */}
                        </ul>
                    </div>
                    <div className="card-footer">
                        <div className="d-flex justify-content-between">
                            <div className="w-100 d-flex justify-content-end align-items-center ">
                                <div className="btn-group">
                                    <button className="btn btn-light">
                                        <b className="text-muted px-2 pt-1">
                                            Total : 
                                            <span className="text-danger mx-2">
                                                {iscdf ? <span className="badge badge-light">CDF</span> : <span className="badge badge-light">USD</span> }
                                                <strong className="px-3">{total}</strong>
                                            </span>
                                        </b>
                                    </button>
                                    <button className="btn btn-primary px-5" onClick={performCommande}>
                                        <span className="fa fa-code-fork pr-2"></span>
                                        <b>Commander</b>
                                    </button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}