import * as React from 'react';
import FlatList from "flatlist-react";
import randomstring from 'randomstring';
import '../assets/fonts/fontawesome/css/all.min.css';
import { endpoint } from "../services/endpoint";
import { Call, _onfetchProductsSearchig } from '../services/fetchingdata';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import Pagination from 'react-responsive-pagination';
import { WhenFailed } from '../components/whenfailed/Whenfailde';
import { WhenEmpty } from '../components/whenempty/Whenempty';
import { Item } from '../components/renderitem/RenderItem';

export const ScreenSearch = (navigation) => {

    const [products, setproducts] = React.useState([]);
    const [currency, setcurrency] = React.useState("USD $");
    const [title, settitle] = React.useState("");
    const [currentPage, setCurrentPage] = React.useState(1);
    const [count, setcount] = React.useState(0);
    const [isfailed, setisfailed] = React.useState(false);
    const [isloading, setisloading] = React.useState(true);

    const ftc = async ({ offset }) => {
        let params = new URLSearchParams(navigation['location']['search']);
        let keyword = params.get("keword") ? params.get("keword") : "david maene"
        settitle(keyword)
        setisloading(true);
        Call.external({
            method: 'GET',
            url: `/produits/list/seachby/${keyword}`
        }, (err, done) => {
            setisloading(false)
            if (done) {
                const { status, message, data } = done;
                if (status === 200) {
                    const { list, length } = data;
                    setcount(length)
                    setproducts(list)
                }else{
                    setproducts([])
                }
            } else {
                setproducts([])
                toast.warning("Erreur de chargement", {
                    position: "bottom-right",
                    autoClose: 5000
                })
            }
        })
    };

    // const 

    React.useEffect(() => {
        // alert(params.get("keword"))
        setcurrency("USD $")
        ftc({ offset: 0 })
    }, [])
    return (
        <>
            <section className="section-name padding-y-sm">
                <div className="container">
                    <header className="section-heading">
                        <a href="/market" className="btn btn-outline-primary float-right">
                            <span className="fa fa-list pr-2"></span>
                            <span>Voir plus</span>
                        </a>
                        <h3 className="section-title">{`Shop > Recherche > `}<b className="text-muted text-capitalize">{`${title}`}</b></h3>
                        <p><b className="text-primary">Résultats</b> : <small className="badge badge-light">{count}</small> Nous selectionnons des produits les plus pertinents pour vous </p>
                    </header>
                    <div className="row">
                        {isfailed ? <WhenFailed /> :
                            <>
                                <FlatList
                                    list={products}
                                    renderWhenEmpty={() => <WhenEmpty />}
                                    renderItem={(item) => <Item item={item} key={item.id} />}
                                    keyExtractor={(item) => item['id'].toString()}
                                />
                                <div className="col-lg-12 pt-3">
                                    <Pagination
                                        current={currentPage}
                                        total={count && count > 0 ? Math.floor(count / 12) : 0}
                                        onPageChange={(page) => { setCurrentPage(page); ftc(page) }}
                                    />
                                </div>
                            </>
                        }
                    </div>
                </div>
            </section>
        </>
    )
}