import * as React from 'react';
import { toast } from 'react-toastify';
import { baseURL, endpoint } from '../services/endpoint';
import 'react-toastify/dist/ReactToastify.css';
import { useHistory } from 'react-router-dom'
import { Call, _onfetchingProductById } from '../services/fetchingdata';
import { Avatar, Card, Col, Row, Skeleton } from 'antd';
import { EditOutlined, SettingsOutlined } from '@mui/icons-material';
import { EllipsisOutlined } from '@ant-design/icons';
const { Meta } = Card;

export const DetailScreen = (navigation) => {
    const [item, setitem] = React.useState(null);
    const [isfailed, setisfailed] = React.useState(false);
    const [isloading, setisloading] = React.useState(true);
    const [associmgs, setassocimgs] = React.useState([]);
    const [articles, setarticles] = React.useState([]);
    let [qte, setqte] = React.useState(1);
    const [index, setindex] = React.useState(0)
    const history = useHistory()

    const assets = [
        {
            id: 1,
            // imgcover: item['imgcover']
        },
        {
            id: 9,
            // imgcover: item['imgcover']
        },
        {
            id: 10,
            // imgcover: item['imgcover']
        },
        {
            id: 11,
            // imgcover: item['imgcover']
        },
        {
            id: 12,
            // imgcover: item['imgcover']
        },
        {
            id: 2,
            // imgcover: item['imgcover']
        },
        {
            id: 3,
            // imgcover: item['imgcover']
        },
        {
            id: 4,
            // imgcover: item['imgcover']
        },
        {
            id: 5,
            // imgcover: item['imgcover']
        },
        {
            id: 6,
            // imgcover: item['imgcover']
        },
        {
            id: 7,
            // imgcover: item['imgcover']
        },
        {
            id: 7,
            // imgcover: item['imgcover']
        }
    ];

    const inceqte = () => {
        const { qte: asqte, __tbl_unitymesure } = item;
        const { unity } = __tbl_unitymesure
        if (parseInt(qte <= parseInt(asqte))) {
            setqte(++qte)
        } else {
            toast.error(`Désolé la quantité disponoble c'est ${asqte} ${unity}`)
        }
    }

    const decqte = () => setqte(qte > 1 ? --qte : qte);

    const WhenLoading = () => {
        return (
            <>
                <div className="w-100 d-flex justify-content-center my-5 py-5">
                    <div className="col-lg-12 text-center">
                        {isloading ? <h4 className="spinner-border spinner-border-lg text-primary"> </h4> : <h4 className="fa fa-dropbox display-5 text-muted" style={{ fontSize: "92px" }}> </h4>}
                        <h5>Aucune information pour l'instant</h5>
                        <h6>Chargement des informations en cours</h6>
                    </div>
                </div>
            </>
        )
    }

    const WhenFail = () => {
        return (
            <>
                <div className="w-100 d-flex justify-content-center my-5 py-5">
                    <div className="col-lg-12 text-center">
                        <span className="fa fa-cloud display-1 pb-2 text-muted" style={{ fontSize: "92px" }}></span>
                        <h5>Aucune information pour l'instant</h5>
                        <h6>Il est possible que vous ayez un problème de connexion cliquer sur le bouton actualiser pour actualiser la page !</h6>
                        <button className="btn btn-light" onClick={() => window.location.reload()}>
                            <span className="fa fa-circle-o-notch pr-2"></span>
                            <b>Actualiser</b>
                        </button>
                    </div>
                </div>
            </>
        )
    };

    const performCommande = () => {
        if (!localStorage.getItem("memo-user")) history.push("/client/login?cb=detail&item=" + index);
        articles.push({
            item,
            qte
        })
        localStorage.setItem("memo-user-articles", JSON.stringify(articles))
        setassocimgs(assets);
        toast.success("Article ajouter au panier avec succès !", {
            position: "bottom-right",
            autoClose: 5000,
            hideProgressBar: true,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
        })
    }

    const RenderItem = ({ item_ }) => {
        return (
            <div className="col-lg-2 col-2 p-0 pl-1 pt-1 ">
                <img src={item && item['imgcover'] ? `${endpoint}/services/ressources/${item['imgcover']}` : ""} alt="thumbnail" className="w-100 p-1 border" />
            </div>
        )
    }

    const ftc = async ({ param }) => {
        setisloading(true);
        param = parseInt(param)
        Call.external({
            method: "GET",
            url: `/produits/produit/${param}`
        }, (err, done) => {
            setisloading(false)
            if (done) {
                const { status, message, data } = done;
                if (status === 200) {
                    setitem(data)
                    setisfailed(false)
                } else {
                    setisfailed(true)
                    toast.error("Erreur de chargement de la requete !")
                    // history.goBack()
                }
            } else {
                setisfailed(true)
                toast.error("Erreur de chargement de la requete !")
                // history.goBack()
            }
        })
    }

    React.useEffect(() => {
        const param = navigation['match']['params']['prod'];
        const list = localStorage.getItem("memo-user-articles");
        setarticles(list ? JSON.parse(list) : [])
        if (param && !isNaN(parseInt(param))) {
            setindex(param);
            // setitem(param && !isNaN(parseInt(param)) ? parseInt(param) : 0);
            ftc({ param });

        } else {
            toast.info("Erreur de la requete !", {
                position: "bottom-right",
                autoClose: 5000,
                hideProgressBar: true,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            })
            return false;
        }
    }, []);
    return (
        <>
            {isloading && !isfailed ?
                <WhenLoading />
                : isfailed ?
                    <WhenFail />
                    :
                    <div className="container py-5">
                        <Row gutter={[24, 24]} style={{ flexGrow: "" }}>
                            <Col span={24} md={18}>
                                <Card
                                    actions={[
                                        <b>{item && item['price_max']}{item && item['currency']} / {item && item['__tbl_unitymesure'] ? item['__tbl_unitymesure']['unity'] : "---"}</b>,
                                        <b>Quantité {item && item['qte']}{item && item['__tbl_unitymesure'] ? item['__tbl_unitymesure']['unity'] : "---"}</b>,
                                        <EllipsisOutlined key="ellipsis" />,
                                    ]}
                                // cover={}
                                >
                                    <Skeleton loading={isloading} avatar active>
                                        <Meta
                                            title={<h3 className="pt-2">{"Produit > Détails > "}<span className="text-muted">{`${item && item['produit']}`}</span></h3> || item && item['produit']}
                                            description={item && item['__tbl_category'] ? item['__tbl_category']['category'] : "---"}
                                        />
                                        <img src={item && item['image'] ? `${baseURL}/${item['image']}` : ""} alt="Article item" srcSet="" className="w-50" />
                                        <Meta
                                            description={item && item['description']}
                                        />
                                    </Skeleton>
                                </Card>
                            </Col>
                            <Col span={24} md={6}>
                                <Card>
                                    <Skeleton loading={isloading} avatar active>
                                        <Meta
                                            title={<h3 className="pt-2">Contrôle</h3> || item && item['produit']}
                                            description={item && item['description'] ? item['description'] : "---"}
                                        />
                                    </Skeleton>
                                    <div className="w-100">
                                        <div className="row mt-4">
                                            <div className="col-lg-12 px-1 mb-2">
                                                <div className="btn-group">
                                                    <button className="btn btn-light" onClick={decqte}>
                                                        <b className="fa fa-minus"></b>
                                                    </button>
                                                    <input className="btn btn-light text-center w-25" value={qte} />
                                                    <button className="btn btn-light" onClick={inceqte}>
                                                        <b className="fa fa-plus"></b>
                                                    </button>
                                                </div>
                                            </div>
                                            <div className="col-lg-12 px-1">
                                                <button className="btn btn-primary w-100" onClick={performCommande}>
                                                    <b className="fa fa-comment-alt pr-2"></b>
                                                    <b>Contacter le vendeur</b>
                                                </button>
                                            </div>
                                        </div>
                                    </div>
                                </Card>
                            </Col>
                        </Row>
                        <div className="row" hidden>
                            <div className="col-lg-8 py-2">
                                <div className="col bg-light border rounded pb-2">
                                    <h3 className="pt-2">{"Produit > Détails > "}<span className="text-muted">{`${item && item['produit']}`}</span></h3>
                                    <p><b>{item && item['__tbl_category'] ? item['__tbl_category']['category'] : "---"}</b></p>
                                    <img src={item && item['image'] ? `${baseURL}/${item['image']}` : ""} alt="Article item" srcSet="" className="w-100" />
                                </div>
                            </div>
                            <div className="col-lg-4 py-2">
                                <div className="col-lg-12 bg-light- px-0">
                                    <div className="w-100 d-flex bg-light justify-content-between px-4 pt-2 border rounded">
                                        <h5 className="text-muted pr-4 pt-2">Controls </h5>
                                        <h3 className="">
                                            <span className="fa fa-cogs p-0"></span>
                                        </h3>
                                    </div>
                                    <div className="w-100 pt-2">
                                        <h4>{item && item['produit']}</h4>
                                        <p>{item && item['description']}</p>
                                        <div className="row border- border-rounded- p-1">
                                            {associmgs.map((element, idx) => (
                                                <RenderItem item_={element} />
                                            ))}
                                        </div>
                                    </div>
                                    <div className="w-100">
                                        <div className="row">
                                            <div className="col-lg-6">
                                                <div className="btn-group">
                                                    <button className="btn btn-light" onClick={decqte}>
                                                        <b className="fa fa-minus"></b>
                                                    </button>
                                                    <input className="btn btn-light text-center w-25" value={qte} />
                                                    <button className="btn btn-light" onClick={inceqte}>
                                                        <b className="fa fa-plus"></b>
                                                    </button>
                                                </div>
                                            </div>
                                            <div className="col-lg-6 px-1">
                                                <button className="btn btn-primary w-100" onClick={performCommande}>
                                                    <b className="fa fa-shopping-cart pr-2"></b>
                                                    <b>Ajouter au panier</b>
                                                </button>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>

            }
        </>
    )
}