import axios from 'axios';
import { endpoint, location } from './endpoint';

const timeout = 25000;
var headers = new Headers();
headers.append("Content-Type", "application/json");
headers.append("Access-Control-Allow-Origin", "*");
headers.append("Access-Control-Allow-Methods", "POST, GET");
headers.append("Accept", "application/json");
// headers.append('Content-Type', 'multipart/form-data');

axios.interceptors.request.use(
    config => {
        let visitor = localStorage.getItem("memo-visitor");
        visitor = JSON.parse(visitor);

        config.headers.ipaddress = visitor && visitor['ip'];
        config.headers.country = visitor && visitor['country'];
        config.headers.city = visitor && visitor['city'];
        config.headers.org = visitor && visitor['org'];
        config.headers.region = visitor && visitor['region'];
        config.headers.accesskey = "d@vid.maene.darone";
        config.headers.appkeyaccess = "$2b$10$AS6GbX37SkQS6skhMOYjveDOuUUgvGz9dvsrCbeylWl/SwMkDDp2G";
        return config;
    },
    rejected => {
        return new Promise.reject(rejected)
    }
);

axios.interceptors.response.use(
    (resposne) => {
        return resposne;
    }
    , error => {
        const er = error.response ? error.response : undefined;
        return er ? er : new Promise.reject(error)
    }
);

export const onRunExternalRQST = async ({
    url,
    data,
    method,
    params
}, cb) => {
    try {
        await axios({
            timeout: timeout,
            method: method ? method : "GET",
            data: data ? data : null,
            params: params ? params : {},
            url: `${endpoint}${url}`,
            headers: {
                ...headers
            }
        })
            .then(res => {
                return cb(undefined, res['data'])
            })
            .catch(err => {
                return cb(err, undefined)
            })
    } catch (error) {
        return cb(error, undefined)
    }
};

const _onGetLocation = async (cb) => {
    try {
        axios({
            method: "GET",
            url: location
        })
            .then(res => {
                cb(undefined, res['data'])
            })
            .catch(err => {
                cb(undefined, err)
            })
    } catch (error) {
        cb(undefined, error)
    }
};

_onGetLocation((rejected, resolved) => {
    if (resolved) {
        localStorage.setItem("mukulima-visitor", JSON.stringify(resolved));
        onRunExternalRQST({
            data: resolved,
            url: `/stats/stat/vsisits/visit/add`,
            method: "POST"
        }, (err, done) => {
            if (done) {

            } else {

            }
        })
    }
})

export const Call = {
    getLocation: _onGetLocation,
    external: onRunExternalRQST
}