import * as React from 'react';
import { Link } from "react-router-dom";
import banner1 from '../../assets/images/banners/1.jpg';
import banner2 from '../../assets/images/banners/2.jpg';
import banner3 from '../../assets/images/banners/3.jpg';
import { Call, _onfetchCategories, onRunExternalRQST } from '../../services/fetchingdata';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { Carousel } from 'react-bootstrap';
import { IoIosArrowForward } from "react-icons/io";
import { Dims } from '../../assets/theme/theme.dims';
import { Colors } from '../../assets/theme/theme.colors';

export const Carroussel = () => {
    const [categories, setcategories] = React.useState([]);
    const [isloading, setisloading] = React.useState(false);

    const ftcCategories = async () => {
        setisloading(true)
        Call.external({
            url: "/categories/l/list",
            method: "GET",
            params: {
                limit: 8,
                offset: 0
            },
        }, (er, done) => {
            console.log('====================================');
            console.log(done);
            console.log('====================================');
            if (done) {
                const { message, status, data } = done;
                if (status === 200) {
                    const { list, length } = data
                    setcategories(list)
                } else {
                    setcategories([])
                }
            } else {
                setcategories([])
            }
        })
    }

    React.useEffect(() => {
        ftcCategories()
    }, [])

    return (
        <section className="section-main bg padding-y">
            <div className="container">
                <div className="row">
                    <aside className="col-md-3">
                        <nav className="card">
                            <ul className="menu-category">
                                {categories.map(categ => {
                                    const { id, category } = categ;
                                    return (
                                        <li>
                                            <Link to={`/products/c/${id}`}>
                                                <IoIosArrowForward size={Dims.iconSize} color={Colors.primaryColor} />
                                                {" "}
                                                {category}
                                            </Link>
                                        </li>
                                    )
                                })}
                            </ul>
                        </nav>
                    </aside>
                    <div className="col-md-9">
                        <Carousel fade>
                            <Carousel.Item>
                                <img
                                    className="d-block w-100"
                                    src={banner1}
                                    alt="First slide"
                                />
                                <Carousel.Caption>
                                    <h3>Carroussel 1</h3>
                                    <p>Nulla vitae elit libero, a pharetra augue mollis interdum.</p>
                                </Carousel.Caption>
                            </Carousel.Item>
                            <Carousel.Item>
                                <img
                                    className="d-block w-100"
                                    src={banner2}
                                    alt="Second slide"
                                />

                                <Carousel.Caption>
                                    <h3>Caroussel 2</h3>
                                    <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit.</p>
                                </Carousel.Caption>
                            </Carousel.Item>
                            <Carousel.Item>
                                <img
                                    className="d-block w-100"
                                    src={banner3}
                                    alt="Third slide"
                                />

                                <Carousel.Caption>
                                    <h3>Carroussel 3</h3>
                                    <p>Praesent commodo cursus magna, vel scelerisque nisl consectetur.</p>
                                </Carousel.Caption>
                            </Carousel.Item>
                        </Carousel>
                    </div>
                </div>
            </div>
        </section>
    )
}