import * as React from 'react';
import FlatList from "flatlist-react";
import randomstring from 'randomstring';
import '../assets/fonts/fontawesome/css/all.min.css';
import { endpoint } from "../services/endpoint";
import { _onfetchProductsAll } from '../services/fetchingdata';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import Pagination from 'react-responsive-pagination';

export const ShopScreen = (navigation) => {

    const [products, setproducts] = React.useState([]);
    const [currency, setcurrency] = React.useState("USD $");
    const [count, settitle] = React.useState("");
    const [currentPage, setCurrentPage] = React.useState(1);
    const [isfailed, setisfailed] = React.useState(false);
    const [isloading, setisloading] = React.useState(true);


    const whenEmpty = () => {
        return(
            <>
                <div className="w-100 d-flex justify-content-center my-5 py-5">
                    <div className="col-lg-12 text-center">
                        {isloading ? <h4 className="spinner-border spinner-border-lg text-primary"> </h4> : <h4 className="fa fa-dropbox display-5 text-muted" style={{fontSize: "92px"}}> </h4>}
                        <h5>Aucune information pour l'instant</h5>
                        <h6>Chargement des informations en cours</h6>
                    </div>
                </div>
            </>
        )
    };
    const WhenFail = () => {
        return(
            <>
                <div className="w-100 d-flex justify-content-center my-5 py-5">
                    <div className="col-lg-12 text-center">
                        <span className="fa fa-cloud pb-2 display-1 text-muted" style={{fontSize: "92px"}}></span>
                        <h5>Aucune information pour l'instant</h5>
                        <h6>Il est possible que vous ayez un problème de connexion cliquer sur le bouton suivant pour actualiser !</h6>
                        <button className="btn btn-light" onClick={() => window.location.reload()}>
                            <span className="fa fa-circle-o-notch pr-2"></span>
                            <b>Actualiser</b>
                        </button>
                    </div>
                </div>
            </>
        )
    };

    const renderItem = (item) => {
        const link = `/product/detail/${item['id']}`;
        const categ = item['__tbl_category'];
        return(
            <div className="recommendation-column recommendation-column-first col-lg-3 p-1 col-6" key={randomstring.generate()} onClick={(e) => {
                e.preventDefault();
                window.location.assign(`${link}`);
            }}>
                <div className="product-container border rounded">
                    <div className="product-image-container pb-1" style={{height: "186px"}}>
                        <img className="product-image" src={`${endpoint}/services/ressources/${item['imgcover']}`} alt="dav.me" />
                    </div>
                    <div className="product-dscr-container">
                        <div className="product-title text-uppercase text-primary">
                            <strong>{`${item['name']}`}</strong>
                        </div>
                        <div className="product-title text-uppercase text-muted">
                            <strong><small>{`${categ['categorie']}`}</small></strong>
                        </div>
                        <div className="product-price">
                            <span className="discount-block">{currency}</span> {item.price}
                        </div>
                        <div className="oriArea">
                            {currency} {item.price + (item.price * .20)}
                            <div className="discount-block">-20%</div>
                        </div>
                        <div class="sold-area text-warning">
                            <div className="fa fa-star"></div>
                            <div className="fa fa-star"></div>
                            <div className="fa fa-star"></div>
                            <div className="fa fa-star-half-full"></div>
                                4.4 |
                            <div className="product-sold-count text-primary">
                                {Math.floor(Math.random() * 100)} Sold
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    };
    const ftc = async (offset) => {

    };

    React.useEffect(() => {
        // const _ = navigation['match']['params'];
        // console.log(navigation['match']['params']['categ']);
        // settitle(_['categ'])
        setcurrency("USD $")
        // setcateg(base64.decode(_['categ']))
        ftc(0)
    }, [])
    return(
        <>
            <section className="section-name padding-y-sm">
                <div className="container">
                    <header className="section-heading">
                        <a href="/market" className="btn btn-outline-primary float-right">
                            <span className="fa fa-list pr-2"></span>
                            <span>Voir plus</span>
                        </a>
                        <h3 className="section-title">{`Super Marché `}</h3>
                        <p>Nous proposons des selectionnons des produits les plus pertinents pour vous </p>
                    </header>
                    <div className="row">
                        {isfailed ? <WhenFail /> :
                            <>
                                <FlatList 
                                    list={products}
                                    renderWhenEmpty={whenEmpty}
                                    renderItem={renderItem}
                                    keyExtractor={(item) => item['id'].toString()}
                                />
                                <div className="col-lg-12 pt-3">
                                    <Pagination
                                        current={currentPage}
                                        total={count && count > 0 ? Math.floor(count / 32) : 0}
                                        onPageChange={(page) => { setCurrentPage(page); ftc(page) }}
                                    />
                                </div>
                            </>
                        }
                    </div>
                </div>
            </section>
        </>
    )
}