export const Category = (navigation) => {
    return(
        <>
            <div className="w-100 d-flex justify-content-center my-5 py-5">
                <div className="col-lg-12 text-center">
                    <span className="fa fa-connectdevelop text-primary display-1 text-muted pb-3" style={{fontSize: "92px"}}></span>
                    <h5>Page en construction</h5>
                    <h6>Ceci arrive lorsqu'une page a été déplacer ou elle est encore en construction</h6>
                    <button className="btn btn-light" onClick={() => window.location.reload()}>
                        <span className="fa fa-circle-o-notch pr-2"></span>
                        <b>Actualiser</b>
                    </button>
                </div>
            </div>
        </>
    )
} 