import * as React from 'react';
import { _onLogin } from '../services/fetchingdata';
import { toast } from 'react-toastify';
// import 'react-toastify/dist/ReactToastify.css';
import { useHistory } from "react-router-dom";


export const LoginScreen = () => {
    const [email, setEmail] = React.useState("");
    const [password, setPassword] = React.useState("");
    const [borderdangeremail, setdangeremail] = React.useState(false);
    const [borderdangerpassword, setdangerpassword] = React.useState(false);
    const [isloading, setisloading] = React.useState(false);
    const [output, setoutput] = React.useState(null);
    const [oneye, setoneye] = React.useState(true);
    const [enc, setenc] = React.useState(true);
    const history = useHistory()

    function validateEmail(email) {
        const re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        // console.log(re.test(String(email).toLowerCase()));
        return re.test((email));
    }

    const onSubmit = async (e) => {
        e.preventDefault();
        setoutput(null)
        if (validateEmail(email) && password.length > 0) {
            setisloading(true);
            setdangeremail(false);
            setdangerpassword(false);

            const formdata = new FormData();
            formdata.append("email", email);
            formdata.append("password", password);

        } else {
            setisloading(false);
            if (!validateEmail(email)) {
                setdangeremail(true);
                toast.warning("L'adresse email entré n'est pas correct !", {
                    position: "bottom-right",
                    autoClose: 5000,
                    hideProgressBar: true,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                })
            }
            else setdangeremail(false);

            if (password.length === 0) {
                toast.warning("Tapper un mot de passe !", {
                    position: "bottom-right",
                    autoClose: 5000,
                    hideProgressBar: true,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                })
                setdangerpassword(true);
            }
            else setdangerpassword(false);
        }
    }

    const toggleeyes = () => {
        setenc(!enc)
        setoneye(!oneye)
    }

    React.useEffect(() => {
        if (window.localStorage.getItem("memo-user")) window.location.replace("/home")
    }, [])
    return (
        <div style={{ marginTop: 210 }}>
            <div className="w-100 d-flex justify-content-center pb-5 my-5 mt-5 h-100">
                <div className="col-lg-4">
                    <div className="w-100 pb-4">
                        <h3>Connexion</h3>
                        <p>Connectez-vous il y des surprises qui vous attendent, il ya des reduction aujourd'hui</p>
                    </div>
                    <form id="form-connexion" onSubmit={onSubmit}>
                        <div className="form-group">
                            <label for="email">Adresse email ou Numero de téléphone</label>
                            <input type="email" onChange={(text) => {
                                setEmail(text.target.value)
                            }} className={`${borderdangeremail ? "border-danger" : ""} form-control`} id="email" name="email-login" placeholder="Enter email" />
                            <small id="emailHelp" className="form-text text-muted text-info d-none">We'll never share your email with anyone else.</small>
                        </div>
                        <div className="form-group">
                            <label for="password">Mot de pass</label>
                            <div className="input-group">
                                <input type={enc ? "password" : "text"} onChange={(text) => {
                                    setPassword(text.target.value)
                                }} className={`${borderdangerpassword ? "border-danger" : ""} form-control`} name="password-login" id="password" placeholder="mot de passe" />
                                <span className="input-group-append">
                                    <button className="btn btn-default border" type="button" onClick={toggleeyes}>
                                        <b className={oneye ? "fa fa-eye" : "fa fa-eye-slash"}></b>
                                    </button>
                                </span>
                            </div>
                        </div>
                        <div className="form-group py-1 text-center">
                            <b className="text-danger">{output}</b>
                        </div>
                        <button type="submit" className="btn btn-primary w-100">
                            {isloading ? <span className="spinner spinner-border spinner-border-sm mx-2"></span> : ""}
                            <b>Connexion</b>
                        </button>
                        <div className="form-check pt-1">
                            <input type="checkbox" className="form-check-input" id="exampleCheck1" />
                            <label className="form-check-label" for="exampleCheck1">rester connecteé ?</label>
                        </div>
                        <div className="form-group pt-4">
                            <a
                                href="#"
                                onClick={(e) => {
                                    e.preventDefault()
                                    history.push('/client/register')
                                }}
                                className="text-underline"
                            >
                                <span className="fa fa-circle pr-2"></span>
                                <b><u>Je n'ai pas de compte je veux créer un compte !</u></b>
                            </a>
                        </div>
                        <div className="form-group pt-1">
                            <a href="mailto:davidmened@gmail.com" className="text-underline">
                                <span className="fa fa-circle pr-2"></span>
                                <b><u>J'ai déjà oublier le mot de passe !</u></b>
                            </a>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    )
}