import { appfixedcommato } from "../assets/appconstants/app.configs";

export const returnPrice = ({ price }) => {
    price = parseFloat(price);
    return price.toFixed(appfixedcommato);
};

export const limitCharacters = ({ string, needlength }) => {
    string = string.toString();
    return string.length < needlength ? string : `${string.substring(0, needlength - 3)} ...`;
}