import * as React from 'react';
import FlatList from "flatlist-react";

import { Call, _onfetchProducts, onRunExternalRQST } from '../../services/fetchingdata';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { Group } from '../goup/Group';
import { Colors } from '../../assets/theme/theme.colors';
import { HeaderSection } from '../headersection/Headersection';
import { Loading } from '../loading/Loading';
import { WhenEmpty } from '../whenempty/Whenempty';
import { WhenFailed } from '../whenfailed/Whenfailde';
import { Item } from '../renderitem/RenderItem';

export const Products = ({ category, navigation }) => {
    const [products, setproducts] = React.useState([]);
    const [currency, setcurrency] = React.useState("USD $");
    const [isfailed, setisfailed] = React.useState(false);
    const [isloading, setisloading] = React.useState(false);

    const ftc = async () => {
        setisloading(true)
        Call.external({
            method: `GET`,
            url: `/produits/listall`
        }, (err, done) => {
            setisfailed(false)
            if (done) {
                const { status, data, message } = done;
                if (status === 200) {
                    const { list } = data
                    setproducts(list)
                } else {
                    setproducts([])
                }
            } else {
                setproducts([])
            }
        })
    };

    React.useEffect(() => {
        setcurrency("USD $")
        ftc()
    }, [])
    return (
        <>
            {isfailed
                ? <WhenFailed />
                :
                <>
                    <section className="section-name padding-y-sm">
                        <div className="container">
                            <HeaderSection
                                link={"/home"}
                                subtitle={"La plus part des visiteurs ont visités ces articles nous pensons qu'ils pourront vous interessés "}
                                title={"Suggestions"}
                            />
                            <div className="row">
                                <FlatList
                                    list={products}
                                    renderWhenEmpty={() => <WhenEmpty isloading={isloading} key={902820} />}
                                    renderItem={(item) => <Item item={item} />}
                                    keyExtractor={(item) => item['id'].toString()}
                                />
                            </div>
                        </div>
                    </section>
                    <>
                        <Group category={1} />
                        <Group category={2} />
                        <Group category={4} />
                        <Group category={7} />
                        <Group category={3} />
                    </>
                </>
            }
        </>
    )
}