import { Colors } from "../../assets/theme/theme.colors"

export const HeaderSection = ({ link, title, subtitle }) => {
    return (
        <header className="section-heading">
            <a
                href="#home"
                className="btn btn-outline-primary float-right"
                style={{ borderColor: Colors.primaryColor, color: Colors.primaryColor }}
            >
                <span className="fa fa-list pr-2" style={{ color: Colors.primaryColor }}></span>
                <span>Voir plus</span>
            </a>
            <h3 className="section-title" style={{ fontWeight: "lighter" }}>{title || "Suggestions"}</h3>
            <p>{subtitle || "La plus part des visiteurs ont visités ces articles nous pensons qu'ils pourront vous interessés "}</p>
        </header>
    )
}