import { Colors } from "../../assets/theme/theme.colors"
import { Loading } from "../loading/Loading"

export const WhenEmpty = ({ isloading }) => {
    return (
        <>
            <div className="w-100 d-flex justify-content-center my-5 py-5">
                <div className="col-lg-12 text-center">
                    {isloading ? <Loading color={Colors.primaryColor} /> : <h4 className="fa fa-dropbox display-5 text-muted" style={{ fontSize: "92px" }}> </h4>}
                    <h5>Aucune information pour l'instant</h5>
                    <h>Chargement des informations en cours</h>
                </div>
            </div>
        </>
    )
}