import * as React from "react";
import '../../assets/css/bootstrap.css';
import '../../assets/css/responsive.css';
import '../../assets/css/ui.css';
import '../../assets/fonts/fontawesome/css/all.min.css';
import appstoricon from '../../assets/images/misc/appstore.png';
import playstoricon from '../../assets/images/misc/playstor.png';

export const BFooter = () => (
    <section className="section-name padding-y bg">
        <div className="container">
            <div className="row">
                <div className="col-md-6">
                    <h3>Téléchargez l'Application</h3>
                    <p>Soyez toujours à jours des nos nouveautés</p>
                </div>
                <div className="col-md-6 text-md-right">
                    <a href="#" className="pr-2">
                        <img src={appstoricon} height="40" alt="appstor misc" />
                    </a>
                    <a href="#" className="pr-2">
                        <img src={playstoricon} height="40" alt="playstor misc" />
                    </a>
                </div>
            </div>
        </div>
    </section>
)