import * as React from "react";
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import '../../assets/css/bootstrap.css';
import '../../assets/css/responsive.css';
import '../../assets/css/ui.css';
import '../../assets/fonts/fontawesome/css/all.min.css';
import logo from '../../assets/images/soko.png';
import '../../assets/js/script.js';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { Dropdown } from 'react-bootstrap';
import { Call } from "../../services/fetchingdata.js";
import { useHistory } from "react-router-dom";
import { appname } from "../../assets/appconstants/app.configs.js";
import base64 from 'base-64';
import { limitCharacters } from "../../helpers/helpers.helpers.js";

export const Header = () => {

    const [itemscart, setitemscart] = React.useState(0);
    const [keysearch, setkeysearch] = React.useState("");
    const [isloggedin, setisloggedin] = React.useState(false);
    const [currentcustomer, setcustomer] = React.useState({});
    const [categories, setcategories] = React.useState([])
    const history = useHistory();

    const handlechange = (e) => {
        e.preventDefault();
        setkeysearch(e.target.value)
    };

    const logout = (e) => {
        e.preventDefault();
        localStorage.removeItem("memo-user");
        history.replace('/client/login');
        // window.location.assign("/client/login");
    };

    const DecorName = () => {
        return (
            <Dropdown className="px-0">
                <Dropdown.Toggle variant="light" id="dropdown-basic" className="px-3">
                    <strong className="text-capitalize">{`${currentcustomer['fsname']} ${currentcustomer['lsname']}`}</strong>
                </Dropdown.Toggle>

                <Dropdown.Menu>
                    <Dropdown.Item href="#/action-1">
                        <span className="fa fa-user-circle pr-2"></span>
                        <span>Profile</span>
                    </Dropdown.Item>
                    <Dropdown.Item href="/client/cart">
                        <span className="fa fa-shopping-cart pr-2"></span>
                        <span>Panier</span>
                    </Dropdown.Item>
                    <Dropdown.Divider />
                    <Dropdown.Item href="#/action-3" onClick={logout}>
                        <span className="fa fa-sign-out pr-2"></span>
                        <span>Déconnexion</span>
                    </Dropdown.Item>
                </Dropdown.Menu>
            </Dropdown>
        )
    };

    const onsubmit = (e) => {
        e.preventDefault();
        if (keysearch.length > 0) {
            history.replace(`/search?keword=${keysearch}`, { keyword: keysearch })
            // window.location.href = (`/search?keword=${keysearch}`)
        } else toast.warning("Entrer un mot pour la recherche !", {
            position: "bottom-right",
            autoClose: 5000,
            hideProgressBar: true,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
        })
    };

    const ftc = async () => {
        Call.external({
            url: `/categories/l/list`,
            method: "GET",
            params: {
                limit: 7,
                offset: 0
            }
        }, (er, done) => {
            if (done) {
                const { message, status, data } = done;
                if (status === 200) {
                    const { list, length } = data
                    setcategories(list)
                } else {
                    setcategories([])
                }
            } else {
                setcategories([])
            }
        })
    }

    React.useEffect(() => {
        ftc()
        const user = window.localStorage.getItem("memo-user");
        if (user) {
            setcustomer(JSON.parse(user))
            setisloggedin(true)
            let items = localStorage.getItem("memo-user-articles");
            if (items) {
                items = JSON.parse(items)
                setitemscart(items.length)
            }
        }
    }, []);

    return (
        <header className="section-header bg-white" style={{ top: 0, position: "fixed", zIndex: 10000, width: "100%" }}>
            <section className="header-main border-bottom">
                <div className="container">
                    <div className="row align-items-center" >
                        <div className="col-lg-2 col-4 bg-blue-">
                            <a href="/" className="brand-wrap">
                                <h4>{appname}</h4>
                                {/* <img
                                    className="logo"
                                    src={logo}
                                    alt="Mukulima Logo"
                                    style={{
                                        position: "absolute",
                                        resize: "inline",
                                        background: "lime",
                                        height: 100,
                                        width: 100
                                    }}
                                /> */}
                            </a>
                        </div>
                        <div className="col-lg-6 col-sm-12">
                            <form action="#" className="search search-name" onSubmit={onsubmit}>
                                <div className="input-group w-100" >
                                    <input type="text" className="form-control pl-4" placeholder="Recherche ..." onChange={handlechange} />
                                    <div className="input-group-append">
                                        <button className="btn btn-primary px-5" type="submit">
                                            <i className="fa fa-search"></i>
                                        </button>
                                    </div>
                                </div>
                            </form>
                        </div>
                        <div className="col-lg-4 col-sm-6 col-12">
                            <div className="widgets-wrap float-md-right">
                                <div className="widget-header mr-3" hidden>
                                    <button href="#" className="icon icon-sm rounded-circle border" onClick={() => {
                                        if (!isloggedin) history.push("/client/login")
                                        else history.push('/client/cart') //window.location.assign("#/client/cart")
                                    }}>
                                        <i className="fa fa-shopping-cart text-primary"></i>
                                    </button>
                                    <span className="badge badge-pill badge-danger notify">{1 && itemscart < 7 ? itemscart : "+7"}</span>
                                </div>
                                <div className="widget-header icontext">
                                    <button href="#" className="icon icon-sm rounded-circle border">
                                        <i className="fa fa-user text-primary"></i>
                                    </button>
                                    <div className="text">
                                        {!isloggedin ? <b>Commencer ici </b> : null}
                                        <div>
                                            {isloggedin
                                                ? <DecorName />
                                                :
                                                <>
                                                    <a
                                                        href="#"
                                                        onClick={(e) => {
                                                            e.preventDefault()
                                                            history.push("/client/login")
                                                        }}
                                                    >
                                                        Connexion
                                                    </a> 
                                                    {" "}|{" "}
                                                    <a
                                                        href="#"
                                                        onClick={(e) => {
                                                            e.preventDefault()
                                                            history.push("/client/register")
                                                        }}
                                                    >
                                                        Créer un compte
                                                    </a>
                                                </>
                                            }
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
            <nav className="navbar navbar-main navbar-expand-lg navbar-light border-bottom">
                <div className="container" style={{ width: "90&" }}>
                    <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#main_nav" aria-controls="main_nav" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="main_nav">
                        <ul className="navbar-nav">
                            <li className="nav-item dropdown">
                                <Link className="nav-link" to="/">Accueil</Link>
                            </li>
                            {categories.map(category => {
                                const { id, category: categ } = category
                                return (
                                    <li className="nav-item">
                                        <Link className="nav-link" to={`/products/c/${id}?_item=${base64.encode(Math.random() * 1000000)}`}>
                                            {limitCharacters({ needlength: 15, string: categ })}
                                        </Link>
                                    </li>
                                )
                            })}
                            <li className="nav-item">
                                <Link className="nav-link" to="/about">A propos de nous</Link>
                            </li>
                        </ul>
                    </div>
                </div>
            </nav>
        </header>
    )
}