import * as React from 'react';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { _onLogin } from '../services/fetchingdata';
import { useHistory } from "react-router-dom";

export const RegisterScreen = () => {

    const [email, setEmail] = React.useState("");
    const [phone, setphone] = React.useState("");
    const [password, setPassword] = React.useState("");
    const [passwordrep, setPasswordrep] = React.useState("");
    const [fsname, setfsname] = React.useState("");
    const [lsname, setlsname] = React.useState("");
    const [avatar, setavatar] = React.useState("");
    const [age, setage] = React.useState("");
    const [cv, setcv] = React.useState("");
    const [output, setoutput] = React.useState("");
    const [gender, setgender] = React.useState("");
    const [creditcard, setcreditcard] = React.useState("");

    const [borderdangeremail, setdangeremail] = React.useState(false);
    const [borderdangerpassword, setdangerpassword] = React.useState(false);
    const [borderdangernom, setdangernom] = React.useState(false);
    const [borderdangerpnom, setdangerpnom] = React.useState(false);
    const [borderdangerage, setdangerage] = React.useState(false);
    const [borderdangergender, setdangergender] = React.useState(false);
    const [borderdangerreppwd, setdangerreppwd] = React.useState(false);
    const [oneye, setoneye] = React.useState(true);
    const [enc, setenc] = React.useState(true);
    const history = useHistory()


    const [isloading, setisloading] = React.useState(false);

    function validateEmail(email) {
        const re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        // console.log(re.test(String(email).toLowerCase()));
        return re.test((email));
    }

    const toggleeyes = () => {
        setenc(!enc)
        setoneye(!oneye)
    }

    const onSubmit = async (e) => {

        e.preventDefault();
        if (validateEmail(email)
            && password.length > 0
            && fsname.length >= 2
            && lsname.length >= 2
            && age.length > 0
            && gender !== "empty") {

            setdangeremail(false);
            setdangerpassword(false);
            setdangernom(false);
            setdangerpnom(false);
            setdangerage(false);
            if (passwordrep === password) {
                // const id = toast.loading("Enregistrement ...")
                // setTimeout(() => {
                //     toast.update(id, {
                //         render: "Connexion établie !",
                //         type: "info", 
                //         autoClose: 5000,
                //         hideProgressBar: true,
                //         closeOnClick: true,
                //         pauseOnHover: true,
                //         draggable: true,
                //         progress: undefined,
                //     })
                // }, 2000);
                const formdata = new FormData();
                formdata.append("fsname", fsname);
                formdata.append("lsname", lsname);
                formdata.append("password", password);
                formdata.append("age", age);
                formdata.append("gender", gender);
                formdata.append("email", email);
                formdata.append("phone", phone);
                formdata.append("creditcard", creditcard);
                formdata.append("categorie", Math.floor(Math.random() * 10));

                setdangerpassword(false);
                setdangerreppwd(false);
                setisloading(true);



            } else {
                setdangerpassword(true)
                setdangerreppwd(true)
                toast.error("Les mots de passes ne sont pas identiques !", {
                    position: "bottom-right",
                    autoClose: 5000,
                    hideProgressBar: true,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                })
            }


        } else {

            setisloading(false);
            if (!validateEmail(email)) setdangeremail(true);
            else setdangeremail(false);

            if (password.length === 0) setdangerpassword(true);
            else setdangerpassword(false);

            if (fsname.length < 2) setdangernom(true);
            else setdangernom(false);

            if (lsname.length < 2) setdangerpnom(true);
            else setdangerpnom(false);

            if (age.length === 0) setdangerage(true);
            else setdangerage(false);

            if (gender === "empty") setdangergender(true)
            else setdangergender(false)

        }
    }

    return (
        <div style={{ marginTop: 210 }}>
            <div className="w-100 d-flex justify-content-center pb-5 my-5 bg-light-">
                <div className="col-lg-4 bg-secondary- py-4">
                    <div className="w-100 pb-4">
                        <h3>Enregistrement</h3>
                        <p>Créer un compte hcomme pour beneficier de toutes les fonctionalités de HCommerce</p>
                    </div>
                    <form id="form-connexion" onSubmit={onSubmit}>
                        <div className="form-group">
                            <label for="nom">Nom <span className="fa fa-asterisk text-danger" style={{ fontSize: "7px" }}></span></label>
                            <input type="text" onChange={(text) => {
                                setfsname(text.target.value)
                            }} className={`${borderdangernom ? "border-danger" : ""} form-control`} id="nom" name="nom-login" aria-describedby="emailHelp" placeholder="Nom" />
                            <small id="emailHelp" className="form-text text-muted text-info d-none">We'll never share your email with anyone else.</small>
                        </div>
                        <div className="form-group">
                            <label for="postnom">Post nom <span className="fa fa-asterisk text-danger" style={{ fontSize: "7px" }}></span></label>
                            <input type="text" onChange={(text) => {
                                setlsname(text.target.value)
                            }} className={`${borderdangerpnom ? "border-danger" : ""} form-control`} id="postnom" name="postnom-login" aria-describedby="emailHelp" placeholder="Postnom" />
                            <small id="emailHelp" className="form-text text-muted text-info d-none">We'll never share your email with anyone else.</small>
                        </div>
                        <div className="row">
                            <div className="col-lg-6">
                                <div className="form-group">
                                    <label for="postnom">Age <span className="fa fa-asterisk text-danger" style={{ fontSize: "7px" }}></span></label>
                                    <input type="date" onChange={(text) => {
                                        setage(text.target.value)
                                    }} className={`${borderdangerage ? "border-danger" : ""} form-control`} id="postnom" name="postnom-login" aria-describedby="emailHelp" placeholder="Postnom" />
                                    <small id="emailHelp" className="form-text text-muted text-info d-none">We'll never share your email with anyone else.</small>
                                </div>
                            </div>
                            <div className="col-lg-6">
                                <div className="form-group">
                                    <label for="gender">Genre <span className="fa fa-asterisk text-danger" style={{ fontSize: "7px" }}></span></label>
                                    <select name="gender" id="gender" className={`${borderdangergender ? "border-danger" : ""} form-control`} onChange={(text) => {
                                        setgender(text.target.value)
                                        // alert(text.target.value)
                                    }}>
                                        <option value="empty">Séléctionner une option</option>
                                        <option value="Male">Masculin</option>
                                        <option value="Feminin">Feminin</option>
                                    </select>
                                    <small id="emailHelp" className="form-text text-muted text-info d-none">We'll never share your email with anyone else.</small>
                                </div>
                            </div>
                        </div>
                        <div className="form-group">
                            <label for="email">Email address <span className="fa fa-asterisk text-danger" style={{ fontSize: "7px" }}></span></label>
                            <input type="text" onChange={(text) => setEmail(text.target.value)} className={`${borderdangeremail ? "border-danger" : ""} form-control`} id="email" name="email-login" placeholder="Enter email" />
                            <small id="emailHelp" className="form-text text-muted text-info d-none">We'll never share your email with anyone else.</small>
                        </div>
                        <div className="form-group">
                            <label for="phone">Numero de téléphone</label>
                            <input type="phone" onChange={(text) => setphone(text.target.value)} className="form-control" id="phone" name="phone-login" placeholder="Phone" />
                            <small id="emailHelp" className="form-text text-muted text-info d-none">We'll never share your email with anyone else.</small>
                        </div>
                        <div className="row">
                            <div className="col-lg-10 col-10">
                                <div className="form-group">
                                    <label for="phone">Carte de credit</label>
                                    <input type="number" min={0} onChange={(text) => setcreditcard(text.target.value)} className="form-control" id="phone" name="phone-login" placeholder="Carte de credit" />
                                    <small id="emailHelp" className="form-text text-muted text-info d-none">We'll never share your email with anyone else.</small>
                                </div>
                            </div>
                            <div className="col-lg-2 col-2">
                                <div className="form-group">
                                    <label for="phone">CV</label>
                                    <input type="number" max={999} min={0} maxLength={3} onChange={(text) => setcv(text.target.value)} className="form-control" id="phone" name="phone-login" placeholder="cv" />
                                    <small id="emailHelp" className="form-text text-muted text-info d-none">We'll never share your email with anyone else.</small>
                                </div>
                            </div>
                        </div>
                        <div className="form-group">
                            <label for="password">Mot de passe <span className="fa fa-asterisk text-danger" style={{ fontSize: "7px" }}></span></label>
                            <div className="input-group">
                                <input type={enc ? "password" : "text"} onChange={(text) => {
                                    setPassword(text.target.value)
                                }} className={`${borderdangerpassword ? "border-danger" : ""} form-control`} name="password-login" id="password" placeholder="mot de passe" />
                                <span className="input-group-append">
                                    <button className="btn btn-default border" type="button" onClick={toggleeyes}>
                                        <b className={oneye ? "fa fa-eye" : "fa fa-eye-slash"}></b>
                                    </button>
                                </span>
                            </div>
                        </div>
                        <div className="form-group">
                            <label for="reppassword">Repetition mot de passe</label>
                            <input type="password" onChange={(text) => setPasswordrep(text.target.value)} className={`${borderdangerreppwd ? "border-danger" : ""} form-control`} id="reppassword" name="phone-login" placeholder="repetition mot de passe !" />
                            <small id="emailHelp" className="form-text text-muted text-info d-none">We'll never share your email with anyone else.</small>
                        </div>
                        <div className="form-group py-1 text-center">
                            <b className="text-danger">{output}</b>
                        </div>
                        <button type="submit" className="btn btn-primary w-100" disabled={isloading ? true : false}>
                            {isloading ? <span className="spinner spinner-border spinner-border-sm mx-2"></span> : ""}
                            <b>Connexion</b>
                        </button>
                        <div className="form-check pt-2 d-none">
                            <input type="checkbox" className="form-check-input" id="exampleCheck1" />
                            <label className="form-check-label" for="exampleCheck1">Check me in</label>
                        </div>
                        <div className="form-group pt-4">
                            <a
                                href="#"
                                className="text-underline"
                                onClick={(e) => {
                                    e.preventDefault()
                                    history.push('/client/login')
                                }}
                            >
                                <span className="fa fa-circle pr-2"></span>
                                <b><u>J'ai déjà un compte !</u></b>
                            </a>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    )
}