import { Colors } from "../../assets/theme/theme.colors";
import { baseURL } from "../../services/endpoint";
import { useHistory } from "react-router-dom";

export const Item = ({ item }) => {
    const { produit, currency, description, price_min, price_max, image, id, __tbl_user, __tbl_unitymesure, __tbl_category } = item
    const link = `/product/detail/${id}`;
    const { category } = __tbl_category;
    const history = useHistory();

    return (
        <div
            className="recommendation-column recommendation-column-first col-lg-3 p-1 col-6"
            style={{
                cursor: "pointer"
            }}
            key={`${id}`}
            onClick={(e) => {
                e.preventDefault();
                history.push(link)
                // window.location.assign(link)
            }}
        >
            <div className="product-container border rounded">
                <div className="product-image-container pb-1" style={{ height: "186px" }}>
                    <img className="product-image" src={`${baseURL}/${image}`} alt="dav.me" />
                </div>
                <div className="product-dscr-container">
                    <div className="product-title text-uppercase text-primary">
                        <strong style={{ color: Colors.primaryColor }}>{`${produit}`}</strong>
                    </div>
                    <div className="product-title text-uppercase text-muted">
                        <strong><small>{`${category}`}</small></strong>
                    </div>
                    <div className="product-price">
                        <span className="discount-block">{currency}</span> {price_max}
                    </div>
                    <div className="oriArea">
                        {currency} {price_min + (price_min * .20)}
                        <div className="discount-block">-20%</div>
                    </div>
                    <div class="sold-area text-warning" hidden>
                        <div className="fa fa-star"></div>
                        <div className="fa fa-star"></div>
                        {/* <div className="fa fa-star"></div> */}
                        <div className="fa fa-star-half-full"></div>
                        3.4 |
                        <div className="product-sold-count text-primary">
                            {Math.floor(Math.random() * 100)} Sold
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
};