import * as React from 'react';
import FlatList from "flatlist-react";
import randomstring from 'randomstring';
import '../../assets/fonts/fontawesome/css/all.min.css';
import base64 from 'base-64';
import { endpoint } from "../../services/endpoint";
import { Call, _onfetchProductsByCategory } from '../../services/fetchingdata';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { appdefaultcurrency } from '../../assets/appconstants/app.configs';
import { returnPrice } from '../../helpers/helpers.helpers';
import { Item } from '../renderitem/RenderItem';
import { WhenEmpty } from '../whenempty/Whenempty';
import { WhenFailed } from '../whenfailed/Whenfailde';
import { HeaderSection } from '../headersection/Headersection';
import { Colors } from '../../assets/theme/theme.colors';
// import Pagination from 'react-responsive-pagination';

export const Group = ({ category }) => {
    const [products, setproducts] = React.useState([]);
    const [categ, setcategory] = React.useState({})
    const [currency, setcurrency] = React.useState(appdefaultcurrency);
    const [title, settitle] = React.useState("");
    const [isloading, setisloading] = React.useState(false);

    const [link, setlink] = React.useState("/products/");
    const [isfailed, setisfailed] = React.useState(false);

    const ftc = async (categ, offset) => {
        setisloading(true)
        Call.external({
            url: `/produits/list/by/${categ}`,
            method: "GET"
        }, (err, done) => {
            setisfailed(false)
            if (done) {
                const { status, data, message } = done;
                if (status === 200) {
                    const { list, __tbl_category } = data;
                    setcategory(__tbl_category)
                    setproducts(list)
                } else {
                    setproducts([])
                }
            } else {
                setproducts([])
            }
        })
    };

    React.useEffect(() => {

        setcurrency("USD $");
        setlink(`/products/${base64.encode(category)}`)
        ftc(category, 0);

    }, [])
    return (
        <>
            <section className="section-name padding-y-sm">
                <div className="container">
                    <HeaderSection
                        link={""}
                        subtitle={""}
                        title={<span>{"Shop > "}<strong style={{ color: Colors.primaryColor }}>{categ && categ['category']}</strong></span>}
                    />
                    <div className="row">
                        {isfailed
                            ? <WhenFailed />
                            :
                            <>
                                <FlatList
                                    list={products}
                                    renderWhenEmpty={() => <WhenEmpty />}
                                    renderItem={(item) => <Item item={item} />}
                                    keyExtractor={(item) => item['id'].toString()}
                                />
                            </>
                        }
                    </div>
                </div>
            </section>
        </>
    )
}