import * as React from "react";
import './assets/css/bootstrap.css';
import './assets/css/responsive.css';
import './assets/css/ui.css';
import './assets/css/style.css';
import './assets/fonts/fontawesome/css/all.min.css';
import './assets/fonts/font-awesome-4.7.0/css/font-awesome.css';
import { Header } from "./components/header/header";
import { Footer } from "./components/footer/footer";
import { ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { Route, Router, Switch } from "react-router-dom";
import { HomeScreen } from "./pages/HomeScreen";
import { CartScreen } from "./pages/CartScreen";
import { DetailScreen } from "./pages/DetailScreen";
import { AboutScreen } from "./pages/AboutScreen";
import { MarketScreen } from "./pages/MarketScreen";
import { LoginScreen } from "./pages/LoginScreen";
import { RegisterScreen } from "./pages/RegisterScreen";
import { ScreenSearch } from "./pages/SearchScreen";
import { ShopScreen } from "./pages/ShopScreen";
import { ConfigProvider } from "antd";
import { Colors } from "./assets/theme/theme.colors";
import { Category } from "./pages/Category";

function App() {

  React.useEffect(() => {

  }, []);

  return (
    <>
      <ConfigProvider
        theme={
          {
            token: {
              colorPrimary: Colors.primaryColor,
              colorSuccess: Colors.primaryColor
            }
          }
        }
      >
        <Header />
        <Switch>
          <div style={{ marginTop: 110 }}>
            <Route exact path="/" component={HomeScreen} />
            <Route exact path="/home" component={HomeScreen} />
            <Route exact path="/client/cart" component={CartScreen} />
            <Route exact path="/product/detail/:prod" component={DetailScreen} />
            <Route exact path="/about" component={AboutScreen} />
            <Route exact path="/products/c/:categ" component={MarketScreen} />
            <Route axact path="/client/login" component={LoginScreen} />
            <Route exact path="/client/register" component={RegisterScreen} />
            <Route exact path="/search" component={ScreenSearch} />
            <Route exact path="/market" component={ShopScreen} />
            <Route exact path="/market/:market" component={Category} />
            {/* <Route exact path="/***" component={FourAndFour} /> */}
          </div>
        </Switch>
        <ToastContainer
          theme="colored" // dark // default
          position="bottom-right"
          autoClose={5000}
          hideProgressBar
          newestOnTop={false}
          closeOnClick
          rtl={false}
          pauseOnFocusLoss
          draggable
          pauseOnHover
        />
        <Footer />
      </ConfigProvider>
    </>
  );
}

export default App;
